@import url(https://fonts.googleapis.com/css2?family=Mulish&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Mulish", sans-serif;
  background-color: black;
  color: white;
}
.navbar {
  background-color: black !important;
  color: white;
}
.navbar-brand {
  font-size: 1.9rem !important;
  text-transform: capitalize;
}
.navbar a {
  color: white;
    font-size: 1.2rem !important;
    text-transform: capitalize;
}
.menu_active {
  color: #fff;
  font-weight: bold;
  border-bottom: 1px solid #565387;
}

.navbar a:hover {
  color: chartreuse !important;
}

/* =======================================================================================
       #Main Landing section
======================================================================================= */

#header {
  width: 100%;
  height: 80vh;
  margin-top: 70px;
}

#header h1 {
  margin: 0 0 10px 0;
  font-size: 48px;
  font-weight: 700;
  color: white;
}

#header .brand-name {
  color: chartreuse;
}

#header h2 {
  color: #484848;
  font-size: 24px;
}

#header .btn-get-started {
  font-weight: 500;
  font-size: 19px;
  letter-spacing: 1px;
  display: inline-block;
  padding: 10px 30px;
  border-radius: 60px;
  transition: 0.5s;
  color: #3498db;
  border: 2px solid #3498db;
  text-decoration: none;
}

#header .btn-get-started:hover {
  background: #3498db;
  color: #fff;
}

.header-img {
  text-align: right;
}

#header .animated {
  animation: up-down 4s ease-in-out infinite alternate-reverse both;
}

@-webkit-keyframes up-down {
  0% {
    transform: translateY(10px);
  }

  100% {
    transform: translateY(-10px);
  }
}

@keyframes up-down {
  0% {
    transform: translateY(10px);
  }

  100% {
    transform: translateY(-10px);
  }
}

/* =======================================================================================
       #contact
======================================================================================= */
.contact_div {
  width: 100%;
  height: 75vh;
}

/* =======================================================================================
       #footer
======================================================================================= */
footer p {
  padding: 10px;
  margin-top: 50px;
}

/* =======================================================================================
       #responsive code
======================================================================================= */
@media (max-width: 991px) {
  #header {
    height: 80vh;
    text-align: center;
  }

  #header .header-img {
    text-align: center;
  }
  #header .header-img img {
    width: 60%;
  }
}

@media (max-width: 768px) {
  #header {
    margin-top: 20px;
  }
  #header h1 {
    font-size: 28px;
  }
  #header h2 {
    font-size: 18px;
    margin-bottom: 30px;
  }
  #header .header-img img {
    width: 70%;
  }
}
